<template>
    <div class="page-container">
      <md-app>
        <md-app-toolbar class="md-primary">
          <md-button class="md-icon-button" @click="toggleMenu" v-if="!menuVisible">
            <md-icon>menu</md-icon>
          </md-button>
          <span class="md-title">trashcanMap</span>
        </md-app-toolbar>

        <md-app-drawer :md-active.sync="menuVisible" md-persistent="full">
          <md-toolbar class="md-transparent" md-elevation="0">
            <span>Navigation</span>

            <div class="md-toolbar-section-end">
              <md-button class="md-icon-button md-dense" @click="toggleMenu">
                <md-icon>keyboard_arrow_left</md-icon>
              </md-button>
            </div>
          </md-toolbar>

          <md-list>
            <md-list-item>
              <md-icon>move_to_inbox</md-icon>
              <span class="md-list-item-text">Inbox</span>
            </md-list-item>

            <md-list-item>
              <md-icon>send</md-icon>
              <span class="md-list-item-text">Sent Mail</span>
            </md-list-item>

            <md-list-item>
              <md-icon>delete</md-icon>
              <span class="md-list-item-text">Trash</span>
            </md-list-item>

            <md-list-item>
              <md-icon>error</md-icon>
              <span class="md-list-item-text">Spam</span>
            </md-list-item>
          </md-list>
        </md-app-drawer>

        <md-app-content style="height: 100%; width: 100%;">
          <GmapMap
            :center="position || {lat:24.144392, lng:120.684112}"
            :zoom="15"
            map-type-id="roadmap"
            style="position: absolute; height: 100%; width: 100%;"
          >
            <GmapMarker
              :key="index"
              v-for="(m, index) in markers"
              :position="m.position"
              opcaity = '1'
              :clickable="true"
              :draggable="false"
              @click="console.log(m)"
            />
          </GmapMap>
        </md-app-content>
      </md-app>
    </div>
</template>

<script>
  import firbase from 'firebase'
  const MARKERS_PATH = firebase.database().ref('/maps/trashcan')
  let options = {
    enableHighAccuracy: true,
    maximumAge: 0
  };
  export default {
    name: 'HelloWorld',
    data() {
      return {
        error: '',
        position: '',
        markers: [],
        menuVisible: false
      }
    },
    methods: {
      getLocation() {
        let vm = this
        if (navigator.geolocation) {
          navigator.geolocation.watchPosition(vm.showPosition, error => {
            console.log(error)
          }, options)
        } else {
          vm.error = '不支援。'
        }
      },
      showPosition(position) {
        let vm = this
        vm.position = {lat: position.coords.latitude, lng: position.coords.longitude}
      },
      toggleMenu() {
        this.menuVisible = !this.menuVisible
      }
    },
    mounted() {
      let vm = this
      this.getLocation()
      MARKERS_PATH.once('value', (snap) => {
        vm.markers = snap.val()
      })
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style type="scss" scoped>
  body {
    height: 100%;
    width: 100%;
    position: relative;
  }
  .page-container {
    height: 100%;
    width: 100%;
    position: absolute;
  }
  .md-app {
    top: 0;
    height: 100%;
    border: 1px solid rgba(#000, .12);
  }
  // Demo purposes only
  .md-drawer {
    width: 230px;
    max-width: calc(100vw - 125px);
  }
  .phone-viewport {
    width: 322px;
    height: 200px;
    display: inline-flex;
    align-items: flex-end;
    overflow: hidden;
    border: 1px solid rgba(#000, .26);
    background: rgba(#000, .06);
  }
</style>



